<template>
    <div class="data-icons">
        <el-row :span="24">
            <el-col :span="span" v-for="(item, index) in data" :key="index">
                <div class="item" :class="[{'item--easy': discount}]">
                    <div class="item-icon" :style="{color: color}">
                        <i :class="item.icon"></i>
                    </div>
                    <div class="item-info">
                        <span>{{item.title}}</span>
                        <h3 :style="{color:color}">
                            <nx-count-up :start="14" :end="item.count"></nx-count-up>
                        </h3>
                    </div>
                </div>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import nxCountUp from '@/components/nx-count-up/index.vue'
export default {
    name: 'nx-data-icons',
    components: {
        nxCountUp
    },
    data() {
        return {
            span: this.option.span || 6,
            data: this.option.data,
            color: this.option.color || 'rgb(63, 161, 255)',
            discount: this.option.discount || false
        }
    },
    props: {
        option: {
            type: Object,
            default: () => {}
        }
    }
    
}
</script>
<style lang="scss" scoped>
.data-icons{
    .item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 80px;
      margin: 0 auto;
    }
    .item-icon {
      margin-top: 3px;
      margin-right: 8px;
      & > i {
        font-size: 46px !important;
      }
    }
    .item-info {
      line-height: 25px;
      & > span {
        color: #999;
        font-size: 12px;
      }
    }
    .item--easy {
      // flex-direction: column;
      & > .item-icon {
        margin: 0;
      }
      & > .item-info {
        margin-top: -15px;
        & > span {
          font-size: 14px;
        }
      }
    }
}
</style>





